
function validator (formSelector) {
    // l·∫•y form t∆∞∆°ng ·ª©ng ƒë·ªÉ d√πng
    var formValidator = document.querySelector(formSelector);
    // l∆∞u c√°c h√†m ki·ªÉm tra c·ªßa form
    var formRules = {
        required: function(value){
            return value ? undefined : 'Vui l√≤ng nh·∫≠p tr∆∞·ªùng n√†y.'
        },
        email: function(value){
                const regex = (/^[a-zA-Z0-9]+@(?:[a-zA-Z0-9]+\.)+[A-Za-z]+$/)
                return regex.test(value) ? undefined : 'Vui l√≤ng nh·∫≠p ƒë√∫ng email.'
        },
        min: function(min){
            return function (value) {
                return value.length >= min ? undefined : `Nh·∫≠p √≠t nh·∫•t ${min} k√≠ t·ª±.`
            }
        },
        confirm: function(value){
            return value === formValidator.querySelector('[name="password"]').value ? undefined : 'M·∫≠t kh·∫©u kh√¥ng kh·ªõp.'
        },
        number: function(value){
            return Number(value) && value > 0 & value%parseInt(value) === 0  ? undefined : 'Vui l√≤ng nh·∫≠p s·ªë l∆∞·ª£ng.'
        },
    };
    // h√†m l·∫•y th·∫ª cha ch·ª©a th·∫ª th√¥ng b√°o l·ªói
    function getParent (selection) {
        while (selection.parentElement) {
            if (selection.parentElement.matches('.form-group')) {
                return selection.parentElement
            } else {
                selection = selection.parentElement
            }
        }
    }
    // h√†m x·ª≠ l√Ω khi validate
    function handleValidate(input) {
        // l·∫•y c√°c rule (h√†m ki·ªÉm tra) c·ªßa input ƒëang blur
        var ruleOfinput = iuputsRules[input.name]
        // l·∫•y th·∫ª ƒë·ªÉ hi·ªán b√°o l·ªói
        var errorElement = getParent(input).querySelector('.form-message');
        // khai b√°o bi·∫øn l∆∞u tr·ªØ l·ªói
        var errorMessage;
        // n·∫øu t·ªìn t·∫°i rule c·ªßa input th√¨ m·ªõi l·∫∑p
        if (ruleOfinput) {
            // l·∫∑p qua c√°c rule (h√†m ki·ªÉm tra) - truy·ªÅn value c·ªßa input v√†o rule 
            for (rule of ruleOfinput) {
                errorMessage = rule(input.value)
                if(errorMessage) break;
            };
        }
        // n·∫øu tr·∫£ ra errorMessage th√¨ g√°n th√¥ng b√°o cho th·∫ª b√°o v√† css l·ªói
        if(errorMessage) {
            getParent(input).classList.add('invalid')
            errorElement.innerText = errorMessage;
        } 
        // n·∫øu tr·∫£ ra errorMessage undefine (kh√¥ng l·ªói) th√¨ b·ªè th√¥ng b√°o
        else {
            errorElement.innerText = '';
            getParent(input).classList.remove('invalid')
        }
        // return l·∫•y errorMessage ƒë·ªÉ x·ª≠ l√Ω submit form
        return !errorMessage;
    }
    // kh·ªèi t·∫°o ƒë·ªëi t∆∞·ª£ng ƒë·ªÉ l∆∞u tr·ªØ c√°c rule t∆∞∆°ng ·ª©ng v·ªõi m·ªói input {'inputname' : [rule1, rule2,..]}
    var iuputsRules = {};
    // m·∫£ng c√°c input trong form
    var inputs = formValidator.querySelectorAll('[name]');
    // duy·ªát qua t·ª´ng input ƒë·ªÉ l·∫•y t√™n c√°c rule ·ªü attr rule (quy ∆∞·ªõc) v√† l·∫Øng nghe s·ª± ki·ªán
    for (var input of inputs) {
        // l·∫•y t√™n c√°c rule (d·∫°ng chu·ªói rule1|rule2|...)
        var rules = input.getAttribute('rules');
        // n·∫øu input ƒë∆∞·ª£c g·∫Øn rules th√¨ m·ªõi ch·∫°y qua
        if (rules) {
            // t√°ch chu·ªói th√†nh m·∫£ng [rule1, rule2,...]
            var rulesArr = rules.split('|');
            // l·∫∑p qua t·ª´ng rule ƒë·ªÉ ki·ªÉm tra n·∫øu c√≥ TH ƒë·∫∑c bi·ªát (min:6)
            for (var rule of rulesArr) {
                var ruleInfo;
                var ruleFunc = formRules[rule];
                //n·∫øu rule d·∫°ng min:6 th√¨ t√°ch ra m·∫£ng l·∫•y [min, 6] - l·∫•y [0] l√† min ƒë·ªÉ get ƒë∆∞·ª£c h√†m min, l·∫•y [1] l√† 6 truy·ªÅn ƒë·ªëi s·ªë cho h√†m min => l·∫•y ƒë∆∞·ª£c h√†m b√™n trong d√πng (value)
                if (rule.includes(':')) {
                    ruleInfo = rule.split(':')
                    ruleFunc = formRules[ruleInfo[0]](ruleInfo[1])
                }
                if (Array.isArray(iuputsRules[input.name])) {
                    iuputsRules[input.name].push(ruleFunc)
                } else {
                    iuputsRules[input.name] = [ruleFunc];
                };
            };
            // l·∫Øng nghe s·ª± ki·ªán blur tr√™n c√°c th·∫ª input
            input.onblur = function(e) {
                var inputTarget = e.target;
                handleValidate(inputTarget);
            }
            // l·∫Øng nghe s·ª± ki√™n change ƒë·ªÉ b·ªè c√°c th√¥ng b√°o v√† hi·ªáu ·ª©ng khi ƒëang ƒëi√™n input
            input.oninput = function(e) {
                var inputTarget = e.target;
                getParent(inputTarget).classList.remove('invalid')
                getParent(inputTarget).querySelector('.form-message').innerText = '';
            }
        }
    };
    // S·ª± ki·ªán submit form
    var _this = this;
    formValidator.onsubmit = function(e) {
        e.preventDefault();
        var isFormValid = true;
        for (let input of inputs) {
            if (!handleValidate(input)) {
                isFormValid = false;
            }
        }
        if (isFormValid) { //n·∫øu mu·ªën submit m·∫∑c ƒë·ªãnh th√¨ b·ªè code b√™n d∆∞·ªõi, thay v√†o formValidator.submit()
            if (typeof _this.onSubmit === 'function') {
                //l·∫•y t·∫•t c·∫£ input c·ªßa form (nodelist)
                // chuy·ªÉn nodelist v·ªÅ d·∫°ng m·∫£ng,
                // duy·ªát m·∫£ng l·∫•y t√™n l√†m key, value l√†m value cho ƒë·ªëi t∆∞·ª£ng l∆∞u gi·ªØ th√¥ng tin submit
                var formValues = Array.from(inputs).reduce(function(values, input){
                    switch (input.type) {
                        case 'radio':
                            if (input.matches(':checked')) {
                                    values[input.name] = input.value;
                                }
                            else if (!values[input.name]) { //ch·∫°y ƒë·∫øn n√∫t cu·ªëi m√† k c√≥ n√∫t n√†o ·∫•n th√¨ m·ªõi tr·∫£ v·ªÅ r√µng
                                    values[input.name] = '';
                                }
                            break;
                        case 'checkbox':
                            if (input.matches(':checked')) {
                                if (!Array.isArray(values[input.name])){
                                    values[input.name] = []
                                }
                                values[input.name].push(input.value)
                            }else if (!values[input.name]) { //ch·∫°y ƒë·∫øn n√∫t cu·ªëi m√† k c√≥ n√∫t n√†o ·∫•n th√¨ m·ªõi tr·∫£ v·ªÅ r√µng
                                values[input.name] = '';
                            }
                            break;
                        case 'file':
                            values[input.name] = input.files;
                            break;
                        default:
                            values[input.name] = input.value;
                    }
                    return values;
                }, {});
                //ch·∫°y h√†m onSubmit v·ªõi ƒë·ªëi s·ªë l√† ƒë·ªëi t∆∞·ª£ng {name: name, password: password,...}
                // h√†m onSubmit s·∫Ω nh·∫≠n data v√† call API 
                _this.onSubmit(formValues);
                
                // reset input v·ªÅ tr·ªëng
                for (let input of inputs) {
                    if(input.name === "password" || input.name === "re-password"){
                        input.value = '';
                    }
                };
            }
        }
    }
}
export default validator